* {
   box-sizing: border-box;
   margin: 0;
   padding: 0;
   text-rendering: optimizeLegibility;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   -webkit-tap-highlight-color: transparent;
   }

   html {
	   font-size: 16px;
	   font-family: -apple-system, BlinkMacSystemFont, "avenir next", avenir,
	   "helvetica neue", helvetica, ubuntu, roboto, noto, "segoe ui", arial,
	   sans-serif;
	   margin: 0;
	   padding: 0;
	   text-decoration-skip-ink: "auto";
   }

   :root {

         --gray-900: #1a202c;
         --gray-800: #2d3748;
         --gray-700: #4a5568;
         --gray-600: #718096;
         --gray-500: #a0aec0;
         --gray-400: #cbd5e0;
         --gray-300: #e2e8f0;
         --gray-200: #edf2f7;
         --gray-100: #f7fafc;

         --red-100: #fff5f5;
         --red-200: #fed7d7;
         --red-300: #feb2b2;
         --red-400: #fc8181;
         --red-500: #f56565;
         --red-600: #e53e3e;
         --red-700: #c53030;
         --red-800: #9b2c2c;
         --red-900: #742a2a;

         --blue-100: #ebf8ff;
         --blue-200: #bee3f8;
         --blue-300: #90cdf4;
         --blue-400: #63b3ed;
         --blue-500: #4299e1;
         --blue-600: #3182ce;
         --blue-700: #2bc5282;
         --blue-800: #2c5282;
         --blue-900: #2a4365;

         --white: white;
         --red: #f56565;
         --text-decoration-color: var(--gray-400);
         --text-color: var(--gray-800);
         --focus-ring-color: var(--blue-500);


   }


   body {
      
      padding: 0;
      margin: calc((100vh /25) *1.563) calc((100vh /25) *1.563) ;
      background-color: var(--gray-700);
      font-weight: 400;
      line-height: 1.563;
      color: var(--blue-400);

   }


   @media (prefers-color-scheme: dark) {
      body{
         color: white;
         background-color: var(--gray-900);
         caret-color: white;

      }
   }

   /* Typography-------------------------------*/


   h1,
   h2,
   h3,
   h4,
   h6,
   h5 {


      margin-bottom: 1rem;
      margin-top: 1em;
      font-weight: bold;



   }

   h1 {
      font-size: 3.052rem;
      letter-spacing: -0.15rem;
      line-height: 1;
   }

   h2{
      font-size: 2.41rem;
      letter-spacing: -0.12rem;
      line-height: 1.2;
   }

   h3{
      font-size: 1.953rem;
      letter-spacing: -0.09rem;
      line-height: 1.2;
   }
   h4{
      font-size: 1.563rem;
      letter-spacing: -0.06rem;
      line-height: 1.3;
   }
   h5{
      font-size: 1.25rem;
      letter-spacing: -0.03rem;
      line-height: 1.4;
   }
   h6{
      font-size: 1rem;
      letter-spacing: 0;
      line-height: 1.5;
   }
   

   p {
      margin-bottom: 11.563rem;
   }

   p > *:last-child {
      margin-bottom: 0;
   }

   a{
      color: inherit;
      text-decoration: underline;
      text-decoration-color: var(--text-decoration-color);
      --webkit-text-decoration-color: var(--text-decoration-color); 
     }

     @media (hover :hover){
        a:hover{
           text-decoration-color: var(--text-decoration-color);
        }
     }

     @media (hover :hover) and (prefers-color-scheme: dark) {
      a:hover{
         text-decoration-color: white;
      }
   }

   
   @media (prefers-color-scheme: dark) {
      a {
         text-decoration-color: var(--gray-700);
         -webkit-text-decoration-color: var(--gray-700);
      }
   }

   a:focus-visible {
      box-shadow: 0 0 0 2px var(--focus-ring-color);
      outline: none;
   }

   small {
      font-size: 0.888rem;
   }
   
   hr {
   border: 1px solid --gray-700;
   margin: 3.052rem 0;
   }
 


   label {
      font-weight: bold;
      display: flex;
   }

   input[type="email"],
   input[type="text"],
   input[type="number"] {
      padding: 0.65rem 0.5rem;
      font-size: 1rem;
      border: 2px solid var(--gray-200);
      background-color: var(--gray-100);
      color: var(--gray-800);
      border-radius: 10px;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
   }

@media (prefers-color-scheme: dark){
   input[type="email"],
   input[type="text"],
   input[type="number"] {
      border-color: var(--gray-700);
      background-color: var(--gray-800);
      color: white;
   }

   input[type="email"]::placeholder,
   input[type="text"]::placeholder,
   input[type="number"]::placeholder,
   select,
   text-area {
      color: var(--gray-500);
   }
}

input[type="email"],
   input[type="text"],
   input[type="number"],
   select {
      width: 400px;
}

select {
   margin: 0;
   -webkit-appearance: none;
   box-sizing: border-box;
   padding: 0.65rem 0.5rem;
   font-size: 1rem;
   border: 2px solid var(--gray-200);
   border-radius: 10px;
   color: var(--gray-700);
   height: auto;
   background-color: var(--gray-100);
   background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 125.304"><path d="M62.652 103.895L0 21.41h125.304" fill="%23343334"/></svg>');
   background-repeat: no-repeat;
   background-size: 1rem;
   background-position: center right 0.5rem;
}


@media (prefers-color-scheme: dark){
   select{
      background-color: var(--gray-800);
      background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 125.304"><path d="M62.652 103.895L0 21.41h125.304" fill="%233ffffff"/></svg>'); 
      border-color: var(--gray-700);
      color: white;
   }
}

textarea{
   -webkit-appearance: none;
   -moz-appearance: none;
   appearance: none;
   font-size: 1rem;
   border: 2px solid var(--gray-200);
   color: var(--gray-700);
   border-radius: 10px;
   resize: vertical;
   background-color: var(--gray-100);
   box-sizing: border-box;
   padding: 0.65rem 0.5rem;
   font-family: -apple-system, BlinkMacSystemFont, 'avenir next', avenir,
   'Helvetica Neue',helvetica, Roboto, Ubuntu, noto, 'segoe ui', arial,
   sans-serif;
}

@media (prefers-color-scheme: dark) {
   textarea{
      color: white;
      border-color:  var(--gray-800);
      background-color: var(--gray-700);  
      }
   }

   input:focus,
   select:focus,
   textarea:focus {
      outline: none;
      border: 2px solid var(--focus-ring-color);
   }

   input:invalid,
   select:invalid,
   textarea:invalid {
      box-shadow: none;
      border: 2px solid var(--red-600);
   }

   input[type="submit"],
   input[type="button"],
   input[type="reset"],
   button {
      padding: 0.5rem 1.25rem;
      font-size: 1rem;
      border-radius: 10px;
      background-color: var(--blue-600);
      border: 2px solid var(--gray-700);
      color: white;
      text-decoration: none;
      font-weight: bold;
      margin-bottom: 1rem;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      display: inline-block;
      line-height: initial;
      transition: background-color 200ms ease-in-out, border 200ms ease-in-out,
         transform 200ms ease-in-out;
      -webkit-touch-callout: none;
      -webkit-user-select: none;
      user-select: none;
   }

   button {
      cursor: pointer;
   }

   .react-pdf__Page__annotations.annotationLayer{
      padding: 20px;
   }